

.todomaindiv{
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
    margin-top: 40px;
    border: 1px solid red;
    width: 50%;
    margin: 40px auto;
    background-color: rgb(85, 69, 69);
    padding: 15px;
    border-radius: 10px;
}

h1{
    color: aliceblue;
    font-size: 30px;
}

input{
    background-color: rgb(196, 182, 182);
    border: none;
    width:300px;
    border-radius: 20px;
    height: 40px;
    padding: 0 10px;
    color: rgb(7, 7, 7);
    font-style: italic;
    font-family: serif;
    font-weight: 700;    

}

.listdiv{
    background-color: rgb(74, 74, 102);
    border-radius: 10px;
    padding: 20px;
    color: white;
    width: 300px;
    text-align: center;
    
}

.listdiv li{
    list-style: none;
    margin-top: 20px;
    display: flex;
    gap: 10px;
    justify-content: space-between;
    align-items: center;
    /* border: 1px solid red; */
    padding: 5px;
    background-color: rgb(141, 93, 93);
    border-radius: 7px;
    font-style: italic;
    font-family: serif;
    color: rgb(64, 240, 167);
    font-weight: 700;
}

button{
    border: none;
    border-radius: 5px;
    width: 70px;
    background-color: rgb(64, 240, 167);
    font-style: italic;
    font-family: serif;
    font-weight: 700;
}

@media screen and (max-width:500px) {

    input{
        width: 200px;
    }
    .todomaindiv{
        width: 90%;
    }

    .listdiv{
        width: 275px;
    }
    
}